import React from 'react';
import './tableComponent.css';

export class TableComponent extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      currentCol: 0,
      currentRow: 0,
      cols: [],
      rows: [],
      delColTransform: 'none',
      delRowTransform: 'none',
      delColDisplay: 'none',
      delRowDisplay: 'none'
    }

    this.createCol = (colsArray) => {
      let colsElementsArray = [];
      colsArray.forEach((col, index) => colsElementsArray.push(
        <div className='col'
          style={{
            width: this.props.cellSize + `px`,
            height: this.props.cellSize + 'px'
          }}
          key={col}
          data-col-index={index}
        ></div>
      ));
      return colsElementsArray;
    }

    this.createRow = (rowsArray) => {
      let rowsElementsArray = [];
      rowsArray.forEach((row, index) => rowsElementsArray.push(
        <div className='row' key={row} data-row-index={index}>{this.createCol(this.state.cols)}</div>
      ));
      return rowsElementsArray;
    }

    this.addCol = this.addCol.bind(this);
    this.addRow = this.addRow.bind(this);
    this.delCol = this.delCol.bind(this);
    this.delRow = this.delRow.bind(this);
    this.delButtonMover = this.delButtonMover.bind(this);
    this.showBtns = this.showBtns.bind(this);
    this.hideBtns = this.hideBtns.bind(this);

  }

  componentDidMount() {
    let rows = [];
    let cols = [];
    for (let i = 0; i < this.props.initialHeight; i++) {
      rows.push(i);
    }
    for (let i = 0; i < this.props.initialWidth; i++) {
      cols.push(i);
    }
    this.setState({ rows });
    this.setState({ cols });
  }

  addCol() {
    let cloneCols = this.state.cols;
    cloneCols.push(this.state.cols[this.state.cols.length - 1] + 1);
    this.setState({ cols: cloneCols });

    if (this.state.cols.length === 2) {
      this.setState({ delColDisplay: 'block' });
    }
  }

  addRow() {
    let cloneRows = this.state.rows;
    cloneRows.push(this.state.rows[this.state.rows.length - 1] + 1);
    this.setState({ rows: cloneRows });

    if (this.state.rows.length === 2) {
      this.setState({ delRowDisplay: 'block' });
    }
  }

  delCol() {
    let cloneCols = this.state.cols;

    if (this.state.cols.length > 1) {
      cloneCols.splice(this.state.currentCol, 1);
      this.setState({ cols: cloneCols });

      if (this.state.cols.length === 1) {
        this.setState({ delColDisplay: 'none' });
      }
    }

    if (this.state.currentCol === this.state.cols.length) {
      this.setState({ currentCol: this.state.currentCol - 1 })
      this.setState({ delColTransform: `translateX(${(this.state.currentCol - 1) * this.props.cellSize + 2}px)` });
    }

  }

  delRow() {
    let cloneRows = this.state.rows;

    if (this.state.rows.length > 1) {
      cloneRows.splice(this.state.currentRow, 1);
      this.setState({ rows: cloneRows });
      
      if (this.state.rows.length === 1) {
        this.setState({ delRowDisplay: 'none' });
      }
    }

    if (this.state.currentRow === this.state.rows.length) {
      this.setState({ currentRow: this.state.currentRow - 1 })
      this.setState({ delRowTransform: `translateY(${(this.state.currentRow - 1) * this.props.cellSize + 2}px` });
    }
  }

  createButtns() {
    return (
      <>
        <div className='add-btns-container'>

          <button className='add add-col'
            onClick={this.addCol}
            style={{
              width: this.props.cellSize + 2 + 'px',
              height: this.props.cellSize + 2 + 'px',
              right: -(this.props.cellSize + 3) + 'px'
            }}
          >+</button>

          <button className='add add-row'
            onClick={this.addRow}
            style={{
              width: this.props.cellSize + 2 + 'px',
              height: this.props.cellSize + 2 + 'px',
              bottom: -(this.props.cellSize + 3) + 'px'
            }}
          >+</button>

        </div>
        <div className='delete-btns-container' style={{ top: -this.props.cellSize + 'px', left: -this.props.cellSize + 'px' }}>

          <button className='delete del-col'
            onClick={this.delCol}
            style={{
              display: this.state.delColDisplay,
              transform: this.state.delColTransform,
              width: this.props.cellSize + 2 + 'px',
              height: this.props.cellSize + 2 + 'px',
              left: this.props.cellSize + 'px'
            }}
          >-</button>

          <button className='delete del-row'
            onClick={this.delRow}
            style={{
              display: this.state.delRowDisplay,
              transform: this.state.delRowTransform,
              width: this.props.cellSize + 2 + 'px',
              height: this.props.cellSize + 2 + 'px',
              top: this.props.cellSize + 'px'
            }}
          >-</button>

        </div>
      </>
    )
  }

  delButtonMover(event) {
    if (event.target.classList.contains('col')) {
      this.setState({ currentCol: +event.target.dataset.colIndex });
      this.setState({ currentRow: +event.target.parentNode.dataset.rowIndex });
    }
    this.setState({ delColTransform: `translateX(${this.state.currentCol * (this.props.cellSize + 2) + 1}px)` });
    this.setState({ delRowTransform: `translateY(${this.state.currentRow * (this.props.cellSize + 2) + 1}px)` });
  }

  showBtns() {
    if (this.state.cols.length > 1) {
      this.setState({ delColDisplay: 'block' });
    }

    if (this.state.rows.length > 1) {
      this.setState({ delRowDisplay: 'block' });
    }
  }

  hideBtns() {
    this.setState({ delColDisplay: 'none' });
    this.setState({ delRowDisplay: 'none' });
  }



  render() {
    return (
      <div className='container-table'
        onMouseMove={this.delButtonMover}
        onMouseEnter={this.showBtns}
        onMouseLeave={this.hideBtns}
      >
        <div className='boxes-container'>
          {this.createRow(this.state.rows)}
        </div>
        {this.createButtns()}
      </div>
    )
  }
}

TableComponent.defaultProps = { initialWidth: 4, initialHeight: 4, cellSize: 50 };
